      margin-left: auto;
    }
  }

  &__title {
    font-size: 30px;
  }

  &__main-search {
    margin: 0 auto 70px;
  }

  &__text {
    margin-bottom: 40px;
  }

  &__box {
    display: flex;
    align-items: center;
    margin: 0 auto;
    width: fit-content;

    .thanks__text {
      margin-bottom: 0;
    }

    @include max-w($sm) {
      align-items: flex-end;
      span {
        display: block;
      }

      .thanks__text {
        margin-bottom: 12px;
      }
    }
  }

  &__copy {
