    @include max-w($sm) {
      margin-left: 5px;
    }
  }

  &__bg {
    position: absolute;
    left: 0;
    bottom: -20px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left bottom;
    z-index: -1;
    height: 80%;
    width: 35%;
    background-color: #fff;

    @include max-w($lg) {
