      bottom: 0;
      width: 36%;
      background-image: linear-gradient(270deg, #ededed 0%, rgba(237, 237, 237, 0) 100%);
    }

    &::before {
      left: 0;
      height: 200px;
      background-image: linear-gradient(0deg, rgba(237, 237, 237, 0) 0%, #ededed 100%);
    }
  }
}

