.look-at-order {
  padding-top: 200px;
  overflow: hidden;
  position: relative;
  min-height: calc(100vh - 170px);
  @include max-w($lg) {
    padding-top: 120px;
    padding-bottom: 80px;
  }

  &__container {
    margin-left: 33%;
    max-width: 1015px;

    @include max-w($xl) {
      margin-left: 200px;
    }

    @include max-w($lg) {
      margin-left: auto;
    }
  }

  &__title {
    margin: 0 auto 45px 0;
    padding-left: 0;

    @include max-w($md) {
      font-size: 25px;
    }
  }

  &__main-search {
    margin: 0 auto 35px 0;
  }

  &__message {
    position: relative;
    font-weight: 700;
    font-size: 25px;
    line-height: 38px;
    margin-bottom: 40px;
    margin-left: 150px;

    @include max-w($lg) {
      margin-left: 40px;
    }

    @include max-w($md) {
      margin-left: 0;
      margin-bottom: 95px;
      font-size: 17px;
    }

    &--small {
      font-weight: 900;
      font-size: 17px;
      line-height: 20px;
    }
  }

  &__number {
    width: fit-content;
    position: relative;
  }

  &__copy-btn {
    position: absolute;
    left: calc(100% + 10px);
    bottom: -5px;
  }

  &__btns-wrapper {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-left: 150px;
    margin-bottom: 40px;
    @include max-w($lg) {
      margin-left: 40px;
    }
    @include max-w($md) {
      flex-direction: row;
      flex-wrap: wrap;
      margin-left: 0;
    }
  }

  &__btn {
    margin-bottom: 10px;

    &--small {
      padding-left: 13px;
      padding-right: 13px;
    }

    @include max-w($md) {
      width: calc(50% - 5px);
      padding-left: 7px;
      padding-right: 7px;
      text-align: center;
      &:first-child {
        width: 100%;
      }

      &:nth-child(2) {
        margin-right: 10px;
      }
    }
  }

  &__info {
    display: flex;
    justify-content: flex-end;
    position: relative;
    margin-bottom: 8px;

    @include max-w($md) {
      justify-content: flex-start;
    }
  }

  &__button {
    @include no-btn;
    font-weight: 500;
    font-size: 17px;
    line-height: 34px;
    padding: 5px 35px 5px 5px;
    position: relative;
    margin-right: 27px;

    svg {
      position: absolute;
      top: 50%;
      right: -22px;
      transform: translateY(-50%);

      &:last-child {
        display: none;
      }

      @include max-w($md) {
        display: none;
        transform: rotate(90deg);
        top: 6px;
        right: -10px;
        transition: transform 0.3s;

        &:last-child {
          display: block;
        }
      }
    }

    &.active {
      @include max-w($md) {
        svg {
          transform: rotate(270deg);
        }
      }
    }
  }

  &__info-text {
    position: relative;
    margin-left: auto;
    padding-top: 10px;
    background-color: rgba(178, 189, 227, 0.59);
    border-radius: 15px;
    padding: 6px 15px;
    max-width: 860px;
    font-weight: 500;
    font-size: 17px;
    line-height: 34px;
    margin-bottom: 30px;
    opacity: 0;
    transition: opacity 0.1;
    @include max-w($md) {
      display: none;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 100%;
      right: 30px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 10px 20px 10px;
      border-color: transparent transparent rgba(178, 189, 227, 0.59) transparent;
      @include max-w($md) {
        display: none;
      }
    }

    &.show {
      opacity: 1;
      display: block;
    }
  }

  &__bg {
    position: absolute;
    z-index: -1;
    left: 0;
    bottom: 0;
    width: 53%;
    height: calc(100% - 79px);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom left;

    @include max-w($xl) {
      left: -320px;
      width: 1024px;
    }

    @include max-w($lg) {
      display: none;
    }

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 45%;
      background-image: linear-gradient(270deg, #ededed 0%, rgba(237, 237, 237, 0) 100%);
      z-index: 0;
    }
  }
}

